import { useLaunchDarkly } from '@/hooks';
import CustomPreviewBodyV2 from '@/screens/Chamber/CustomPreview/CustomPreviewBodyV2';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { ChamberBody } from './ChamberBody';
import { NoumBody } from './NoumBody';

export const NoumView = () => {
  const { isConnected, isOwner } = useNoumContext();

  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();

  return (
    <>
      {noumEditor2 ? (
        isConnected || isOwner ? (
          <NoumBody />
        ) : (
          <CustomPreviewBodyV2 />
        )
      ) : (
        <ChamberBody />
      )}
    </>
  );
};
